.form-completion-success-alert {
  max-width: 1100px;
  margin-left: auto;
  margin-right: auto;
}
.registration-statuses {
  max-width: 650px;
  margin-left: auto;
  margin-right: auto;
  padding: 50px;
  margin-top: 100px;
  position: relative;
  background-color: #C0C0C0;
}
.legend {
  position: absolute;
  top: -30px;
  left: -25px;
  z-index: 10000;
  background: #85E6F2;
  padding: 10px;
  border-radius: 8px;
  color: #FFF;
  font-size: 20px;
}
